<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Masters</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span> Meetings </span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>
         
        <!-- offcanvas start -->
        <div class="offcanvas offcanvas-end" data-bs-scroll="true" tabindex="-1" id="offcanvasWithBothOptions" aria-labelledby="offcanvasWithBothOptionsLabel">
            <div class="offcanvas-header justify-content-between">
                <h5 class="offcanvas-title" id="offcanvasWithBothOptionsLabel">MEETING LISTS</h5>
                <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            <div v-if="isloading" v-html="waitIndicator()"></div>
            <div v-else class="offcanvas-body">
                <div class="row">
                    <div class="col-12">
                        <!-- Search input for meetings -->
                        <div class="mb-3 d-flex gap-2 align-items-center">
                            <div class="flex-grow-1">
                                <global-search-field
                                    v-model:filterValue="meetingsFilter"
                                />
                            </div>
                            <button class="btn btn-primary" @click="getUsers()" data-bs-toggle="modal" data-bs-target="#register-meeting-modal">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus" viewBox="0 0 16 16">
                                    <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
                                </svg>
                            </button>
                        </div>
                        
                        <div class="list-group text-center" id="list-tab" role="tablist">
                            <a 
                                v-for="meeting in filteredMeetings" 
                                :key="meeting.id" 
                                @click="selectMeeting(meeting)"
                                class="list-group-item list-group-item-action" 
                                :class="{ 'active': selected_meeting_id === meeting.id }"
                                data-bs-dismiss="offcanvas"
                                href="javascript:void(0);"
                            >
                                {{ meeting.name }}
                            </a>
                        </div>
                        
                        <!-- Show message when no meetings match search -->
                        <div v-if="filteredMeetings.length === 0 && meetingsFilter" class="text-center mt-3 text-muted">
                            No meetings found matching "{{ meetingsFilter }}"
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- offcanvas ends -->
  
        <div class="d-flex justify-content-end mb-3">
            <button 
                class="btn btn-primary me-2" 
                v-if="selected_meeting_id && user_permissions.includes('can_edit_meetings')"
                data-bs-toggle="modal" 
                data-bs-target="#edit-meeting-modal"
                @click="OpenEditMeeting(selected_meeting)" 
            >
               <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            stroke-width="1.5"
                                            stroke-linecap="round"
                                            stroke-linejoin="round"
                                            class="text-light"
                                        >
                                            <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                                            <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                                        </svg>
            </button>
                                      <button class="btn btn-success me-2 " v-if ="selected_meeting_id"
                             @click="printMeetingReport()" 
                            >                           
                              <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
  <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
  <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
</svg>
                             </button>
            <button class="btn" v-if="mobileView"  data-bs-toggle="offcanvas" data-bs-target="#offcanvasWithBothOptions" aria-controls="offcanvasWithBothOptions">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-list-ul" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M5 11.5a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m-3 1a1 1 0 1 0 0-2 1 1 0 0 0 0 2m0 4a1 1 0 1 0 0-2 1 1 0 0 0 0 2m0 4a1 1 0 1 0 0-2 1 1 0 0 0 0 2"/>
                </svg>
            </button>
            
            <button v-else class="btn btn-outline-primary" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasWithBothOptions" aria-controls="offcanvasWithBothOptions">Select Meeting from the lists</button>
        </div>

        <div v-if="!selected_meeting_id" class="text-center my-3 text-muted" style="font-size: 1.1rem;">
            Please select a Meeting from the list to view its details
        </div>

         
        <div v-if="selected_meeting">
            <div class="row layout-top-spacing">
                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Meeting Name</label>
                    <div class="p-3 border rounded text-center bg-purple-light">
                        <p class="form-control-plaintext mb-0">{{ selected_meeting?.name }}</p>
                    </div>
                </div>
                
                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Date</label>
                    <div class="p-3 border rounded text-center bg-purple-light">
                        <p class="form-control-plaintext mb-0">{{ selected_meeting?.date }}</p>
                    </div>
                </div>
                
                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Organizer</label>
                    <div class="p-3 border rounded text-center bg-purple-light">
                        <p class="form-control-plaintext mb-0">{{ selected_meeting?.organizer?.name }}</p>
                    </div>
                </div>

                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Meeting Time</label>
                    <div class="p-3 border rounded text-center bg-grey-light">
                        <p class="form-control-plaintext mb-0">{{ formatTime(selected_meeting?.meeting_time) }}</p>
                    </div>
                </div>
                
                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Meeting Duration</label>
                    <div class="p-3 border rounded text-center bg-grey-light">
                        <p class="form-control-plaintext mb-0">{{ formatDuration(selected_meeting?.duration) }}</p>
                    </div>
                </div>
                
                <div class="col-md-4">
                    <label class="form-label fw-bold text-center d-block text-dark">Meeting Location</label>
                    <div class="p-3 border rounded text-center bg-grey-light">
                        <p class="form-control-plaintext mb-0">{{ selected_meeting?.location }}</p>
                    </div>
                </div>
            </div>

            <div class="row mt-3">
                <div class="col-md-12">
                    <div class="p-3 border rounded text-center bg-purple">
