                                <p class="form-control-plaintext mb-0 fw-bold fs-5" style="color: #5B2C6F;">Meeting Attendees</p>
                                <div v-if="!showAttendeeForm" class="flex-grow-1 d-flex gap-2 align-items-center">
                                    <button class="btn btn-outline-primary" @click="showAttendeeForm = true; openAttendeeModal() ; getUsers()" v-if="user_permissions.includes('can_edit_meetings')">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus" viewBox="0 0 16 16">
                                            <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
                                        </svg>
                                    </button>
                                    <button class="btn btn-success" @click="printMeetingAtendees()" v-if="!selected_meeting?.attendance_attachment && user_permissions.includes('can_edit_meetings')">
