                                        </svg>
                                    </button>
                                    <button class="btn btn-primary" v-if="!selected_meeting?.attendance_attachment && user_permissions.includes('can_edit_meetings')" @click="openAttachmentModal()" data-bs-toggle="modal" data-bs-target="#attendance-attachment-modal">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-paperclip text-light" viewBox="0 0 16 16">
                                            <path d="M4.5 3a2.5 2.5 0 0 1 5 0v9a1.5 1.5 0 0 1-3 0V5a.5.5 0 0 1 1 0v7a.5.5 0 0 0 1 0V3a1.5 1.5 0 1 0-3 0v9a2.5 2.5 0 0 0 5 0V5a.5.5 0 0 1 1 0v7a3.5 3.5 0 1 1-7 0z"/>
                                        </svg>
                                    </button>
                                    <a :href="`https://bushman.ink/${selected_meeting?.attendance_attachment}`" class="btn btn-secondary" v-if="selected_meeting?.attendance_attachment" target="_blank">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-eye text-light" viewBox="0 0 16 16">
                                            <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z"/>
                                            <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0"/>
                                        </svg>
                                    </a>
                                </div>
                            </div>
                            <div v-if="showAttendeeForm" class="mt-3">
                                <!-- Attendee Type Selection -->
                                <multiselect 
                                    v-model="attendeeType"
                                    :options="['Company Employee', 'Non Company Employee']"
                                    placeholder="Select attendee type"
                                    :searchable="false"
                                    :allow-empty="false"
                                    class="mb-2"
                                ></multiselect>
                                
                                <!-- Company Employee Selection -->
                                <multiselect 
                                    v-if="attendeeType === 'Company Employee'"
                                    v-model="attendee"
                                    :options="userList"
                                    placeholder="Select company employee"
                                    label="name"
                                    :searchable="true"
                                    :allow-empty="true"
                                    class="mb-2"
                                ></multiselect>
                                
                                <!-- Non Company Employee Input -->
                                <input 
                                    v-if="attendeeType === 'Non Company Employee'"
                                    type="text"
                                    v-model="externalAttendeeName"
                                    class="form-control mb-2"
                                    placeholder="Enter Non Company Employee name"
                                />
                                
                                <div class="d-flex gap-2">
                                    <button class="btn btn-success flex-grow-1" @click="registerAttendee()" :disabled="!canRegisterAttendee || attending">
                                        <span v-if="attending" class="spinner-border spinner-border-sm" role="status"></span>
                                        <svg v-else xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-check" viewBox="0 0 16 16">
                                            <path d="M10.97 4.97a.75.75 0 0 1 1.07 1.05l-3.99 4.99a.75.75 0 0 1-1.08.02L4.324 8.384a.75.75 0 1 1 1.06-1.06l2.094 2.093 3.473-4.425z"/>
                                        </svg>
                                    </button>
                                    <button class="btn btn-secondary flex-grow-1" @click="cancelAddAttendee()">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x" viewBox="0 0 16 16">
                                            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
                                        </svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="p-0">
                            <table class="table table-hover mb-0">
                                <tbody>
                                    <tr v-for="attendee in selected_meeting.attendees" :key="attendee.id">
                                        <td class="border-end">{{ attendee.name }}</td>
                                        <td class="border-end">
                                            <span :class="`badge bg-${attendee.status.color}`">
                                                {{ attendee.status.name }}
                                            </span>
                                        </td>
                                        <td>
                                            <button 
                                                v-if="attendee && user_permissions.includes('can_edit_meetings')"
                                                class="btn btn-sm btn-outline-primary" 
                                                data-bs-toggle="modal" 
                                                data-bs-target="#edit-attendance-modal"
                                                @click="openAttendeeModal(attendee)"
                                                
                                            >
                                                attendance
                                            </button>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <!-- Meeting Agenda Column -->
                <div class="col-md-4">
                    <div class="border rounded h-100 bg-white">
                       <div class="p-3 border-bottom bg-purple-light">
                            <div class="d-flex gap-2 mt-2 align-items-center">
                                <p class="form-control-plaintext mb-0 fw-bold fs-5" style="color: #5B2C6F;">Meeting Agenda</p>
                                <div v-if="!showAgendaForm" class="flex-grow-1">
                                    <button class="btn btn-outline-primary w-100" @click="showAgendaForm = true" v-if="user_permissions.includes('can_edit_meetings')">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus me-2" viewBox="0 0 16 16">
                                            <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
                                        </svg>
                                    </button>
                                </div>
                            </div>
                            <div v-if="showAgendaForm" class="mt-3">
                                <input 
                                    type="text"
                                    v-model="agendaDescription"
                                    class="form-control mb-2"
                                    placeholder="Enter agenda description"
                                />
                                <div class="d-flex gap-2">
                                    <button class="btn btn-success flex-grow-1" @click="registerAgenda()" :disabled="!agendaDescription || loadingAgenda">
                                        <span v-if="loadingAgenda" class="spinner-border spinner-border-sm" role="status"></span>
                                        <svg v-else xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-check" viewBox="0 0 16 16">
                                            <path d="M10.97 4.97a.75.75 0 0 1 1.07 1.05l-3.99 4.99a.75.75 0 0 1-1.08.02L4.324 8.384a.75.75 0 1 1 1.06-1.06l2.094 2.093 3.473-4.425z"/>
                                        </svg>
                                    </button>
                                    <button class="btn btn-secondary flex-grow-1" @click="cancelAddAgenda()">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x" viewBox="0 0 16 16">
                                            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
                                        </svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="p-0">
                            <table class="table table-hover mb-0">
                                <tbody>
                                    <tr v-for="agenda in selected_meeting?.agendas" :key="agenda.id">
                                        <td class="border-end">{{ agenda.description }}</td>
                                        <td>
                                            <span :class="`badge bg-${agenda?.status?.color}`">
                                                {{ agenda?.status?.name }}
                                            </span>
                                        </td>
                                        <td>
                                            <button 
                                                class="btn btn-sm btn-outline-primary" 
                                                data-bs-toggle="modal" 
                                                data-bs-target="#edit-agenda-modal"
                                                @click="selectedAgenda = agenda ; openAgendaModal(agenda)"
                                                v-if="user_permissions.includes('can_edit_meetings')"
                                            >
                                                agenda
                                            </button>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <!-- Meeting Minutes Column -->
                <div class="col-md-4">
                    <div class="border rounded h-100 bg-white">
                        <div class="p-3 border-bottom bg-purple-light">
                            <div class="d-flex gap-2 mt-2 align-items-center">
                                <p class="form-control-plaintext mb-0 fw-bold fs-5" style="color: #5B2C6F;">Meeting Minutes</p>
                                <div v-if="!showMinutesForm" class="flex-grow-1">
                                    <button class="btn btn-outline-primary w-100" @click="showMinutesForm = true" v-if="user_permissions.includes('can_edit_meetings')">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-plus me-2" viewBox="0 0 16 16">
                                            <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
                                        </svg>
                                    </button>
                                </div>
                            </div>
                            <div v-if="showMinutesForm" class="mt-3">
                                <input 
                                    type="text"
                                    v-model="minuteDescription"
                                    class="form-control mb-2"
                                    placeholder="Enter minute description"
                                />
                                <div class="d-flex gap-2">
                                    <button class="btn btn-success flex-grow-1" @click="registerMinutes()" :disabled="!minuteDescription || loadingMinutes">
                                        <span v-if="loadingMinutes" class="spinner-border spinner-border-sm" role="status"></span>
                                        <svg v-else xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-check" viewBox="0 0 16 16">
                                            <path d="M10.97 4.97a.75.75 0 0 1 1.07 1.05l-3.99 4.99a.75.75 0 0 1-1.08.02L4.324 8.384a.75.75 0 1 1 1.06-1.06l2.094 2.093 3.473-4.425z"/>
                                        </svg>
                                    </button>
                                    <button class="btn btn-secondary flex-grow-1" @click="cancelAddMinute()">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x" viewBox="0 0 16 16">
                                            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"/>
                                        </svg>
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="p-0">
                            <table class="table table-hover mb-0">
                                <tbody>
                                    <tr v-for="minute in selected_meeting.minutes" :key="minute.id">
                                        <td class="border-end">{{ minute.description }}</td>
                                        <td>
                                            <span :class="`badge bg-${minute.status.color}`">
                                                {{ minute.status.name }}
                                            </span>
                                        </td>
                                        <td>
                                            <button 
                                                class="btn btn-sm btn-outline-primary" 
                                                data-bs-toggle="modal" 
                                                data-bs-target="#edit-minutes-modal"
                                                @click="selectedMinute = minute ; openMinutesModal(minute)"
                                                v-if="user_permissions.includes('can_edit_meetings')"
                                            >
                                                minutes
                                            </button>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

                <!-- register meeting modal starts -->
<div class="modal fade mymodal" id="register-meeting-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="registerMeetingModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="registerMeetingModalLabel">Register Meeting</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="closeRegisterModal()"></button>
      </div>
      <form @submit.prevent="registerMeeting()">
        <div class="modal-body">
            <div class="row mb-3">
