</template>

<script setup>
    import { onMounted, ref, inject, computed, watch } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import globalSearchField from '@/components/reusables/globalSearchField.vue';
    import { globalSearch } from '@/components/reusables/globalSearch';
    const waitIndicator = inject('waitIndicator');
    
    useMeta({ title: 'Meetings' });  
    
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const user = JSON.parse(localStorage.getItem("user"));
    const formatDate = inject('formatDate');
    const tableShimmer = inject('tableShimmer');
    const loadingSpinner = inject('loadingSpinner'); 
    
    const loading_spinner = ref(false);    
    const isloading = ref(false);
    const meetings = ref([]);
    const selected_meeting = ref(null);
    const selected_meeting_id = ref(null);
    
    const meetingsFilter = ref('');
    const mobileView = ref(window.innerWidth < 768);

    const printReportMeeting = inject('printReportMeeting');
  const printMeetingReport = () => {
  printReportMeeting(selected_meeting.value);
};

    // Handle window resize for mobile view
    window.addEventListener('resize', () => {
        mobileView.value = window.innerWidth < 768;
    });

    const filteredMeetings = computed(() => {
        return globalSearch(meetings.value, meetingsFilter.value);
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    // Helper functions
    const formatTime = (timeString) => {
        if (!timeString) return 'N/A';
        const time = new Date(`1970-01-01T${timeString}`);
        return time.toLocaleTimeString('en-US', { hour: '2-digit', minute: '2-digit', hour12: true });
    };

    const formatDuration = (durationString) => {
        if (!durationString) return 'N/A';
        const [hours, minutes, seconds] = durationString.split(':');
        const totalMinutes = parseInt(hours) * 60 + parseInt(minutes);
        
        if (totalMinutes >= 60) {
            const hours = Math.floor(totalMinutes / 60);
            const minutes = totalMinutes % 60;
            return minutes > 0 ? `${hours}h ${minutes}m` : `${hours}h`;
        }
        return `${totalMinutes}m`;
    };


    const getMeetings = async () => {
        isloading.value = true;
        try {
            const response = await axiosInstance.get('meetings');
            meetings.value = response.data;
        } catch (error) {
          console.log(error)
            showError(error.message);
        } finally {
            isloading.value = false;
        }
    };

   const userList = ref([]);
    const getUsers = async () => {
        try {
            const response = await axiosInstance.get('users');
            userList.value = response.data;
        } catch (error) {
            showError(error.message);
        }
    };


     const submitting = ref(false);
     const name = ref('');
     const date = ref('');
     const organizer = ref('');
     const meeting_time = ref('');
     const duration = ref('');
     const location = ref('');

     const meeting_name = ref('');
     const meeting_date = ref('');
     const meeting_organizer = ref('');
     const meeting_times = ref('');
     const meeting_duration = ref('');
     const meeting_location = ref('');

    const registerMeeting = async () => {
        submitting.value = true;
        const itemData = {
            name: meeting_name.value,
            date: meeting_date.value,
            organizer_id: meeting_organizer.value.id,
            meeting_time: meeting_times.value,
            duration: meeting_duration.value,
            location: meeting_location.value
        };

        try {
            const response = await axiosInstance.post('/meetings', itemData);
            showMessage('Meeting registered successfully!');
            document.getElementById('close_register').click();
            closeRegisterModal();
            await getMeetings(); 
            if (response.data && response.data.id) {
                selected_meeting_id.value = response.data.id;
            }
            // Dismiss the offcanvas
            const offcanvasElement = document.getElementById('offcanvasWithBothOptions');
            if (offcanvasElement) {
                const offcanvas = bootstrap.Offcanvas.getInstance(offcanvasElement);
                if (offcanvas) {
                    offcanvas.hide();
                }
            }
        } catch (error) {
            showError(error.message);
        } finally {
            submitting.value = false;
        }
    };

     const attending = ref(false);
     const showAttendeeForm = ref(false);
     const attendee = ref(null);
     const attendeeType = ref('');
     const externalAttendeeName = ref('');

     const canRegisterAttendee = computed(() => {
        if (attendeeType.value === 'Company Employee') {
            return attendee.value;
        } else if (attendeeType.value === 'Non Company Employee') {
            return externalAttendeeName.value?.trim();
        }
        return false;
     });

     const registerAttendee = async () => {
        attending.value = true;
        
        let itemData;
        if (attendeeType.value === 'Company Employee') {
            itemData = {
                meeting_id: selected_meeting_id.value,
                name: attendee.value?.name,
                attendee_id: attendee.value?.id,
            };
        } else {
            itemData = {
                meeting_id: selected_meeting_id.value,
                name: externalAttendeeName.value,
                attendee_id: null,
            };
        }

        try {
            await axiosInstance.post('/meeting-attendees', itemData);
            // Reset form and hide it
            attendee.value = null;
            attendeeType.value = '';
            externalAttendeeName.value = '';
            showAttendeeForm.value = false;
            fetchMeetingDetails(selected_meeting_id.value);
        } catch (error) {
            console.log(error);
            showError(error.message);
        } finally {
            attending.value = false;
        }
    };

     const cancelAddAttendee = () => {
        attendee.value = null;
        attendeeType.value = '';
        externalAttendeeName.value = '';
        showAttendeeForm.value = false;
    };

     const loadingAgenda = ref(false);
     const showAgendaForm = ref(false);
     const agendaDescription = ref('');
    const registerAgenda = async () => {
        loadingAgenda.value = true;
        const itemData = {
            meeting_id: selected_meeting_id.value,
            description : agendaDescription.value
        };
    
        try {
            await axiosInstance.post('/meeting-agendas', itemData);
            showMessage('Agenda registered successfully!');
            agendaDescription.value = '';
            showAgendaForm.value = false;
            fetchMeetingDetails(selected_meeting_id.value);
        } catch (error) {
            showError(error.message);
        } finally {
            loadingAgenda.value = false;
        }
    };

    const cancelAddAgenda = () => {
        agendaDescription.value = '';
        showAgendaForm.value = false;
    };

    const loadingMinutes = ref(false);
    const showMinutesForm = ref(false);
    const minuteDescription = ref('');
    const registerMinutes = async () => {
        loadingMinutes.value = true;
        const itemData = {
            meeting_id: selected_meeting_id.value,
            description : minuteDescription.value
        };
    
        try {
            await axiosInstance.post('/meeting-minutes', itemData);
            showMessage('Minute registered successfully!');
            minuteDescription.value = '';
            showMinutesForm.value = false;
            fetchMeetingDetails(selected_meeting_id.value);
        } catch (error) {
            showError(error.message);
        } finally {
            loadingMinutes.value = false;
        }
    };
    const cancelAddMinute = () => {
        minuteDescription.value = '';
        showMinutesForm.value = false;
    };

    const closeRegisterModal = () => {
        meeting_name.value = '';
        meeting_date.value = '';
        meeting_organizer.value = '';
        meeting_times.value = '';
        meeting_duration.value = '';
        meeting_location.value = '';
    };

watch(selected_meeting_id, async (newId, oldId) => {
    if (newId && newId !== oldId) {
        await fetchMeetingDetails(newId);
    }
});
    
const fetchMeetingDetails = async (meetingId) => {
    if (!meetingId) return;
    
    try {
        const response = await axiosInstance.get(`meetings/${meetingId}`);
                if (Array.isArray(response.data)) {
            const meeting = response.data.find(m => m.id === parseInt(meetingId));
            selected_meeting.value = meeting || null;
        } else {
            selected_meeting.value = response.data;
        }
      
    } catch (error) {
        showError(error.message);
        console.error(error);
    }
};

const selectMeeting = (meeting) => {
    selected_meeting_id.value = meeting.id;
};


const selectedAgenda = ref(null);
const selectedMinute = ref(null);
const selectedAttendee = ref(null);
const agenda_status = ref(null);
const minutes_status = ref(null);
const attendee_status = ref(null);

const updateStatus = async (endpoint) => {
    loading_spinner.value = true;
    let statusId;
    
    // Determine which status to use based on endpoint
    if (endpoint.includes('meeting-attendees')) {
        statusId = attendee_status.value?.id || attendee_status.value;
    } else if (endpoint.includes('meeting-agendas')) {
        statusId = agenda_status.value?.id || agenda_status.value;
    } else if (endpoint.includes('meeting-minutes')) {
        statusId = minutes_status.value?.id || minutes_status.value;
    }
    
    const payload = {
        meeting_id: selected_meeting_id.value,
        status_id: statusId,
    };
    
    try {
        const response = await axiosInstance.put(endpoint, payload);
        fetchMeetingDetails(selected_meeting_id.value);
        showMessage('Status updated successfully!');
        if (endpoint.includes('meeting-attendees')) {
            document.getElementById('close_attendee').click();
        } else if (endpoint.includes('meeting-agendas')) {
            document.getElementById('close_agenda').click();
        } else if (endpoint.includes('meeting-minutes')) {
            document.getElementById('close_minutes').click();
        }
        closeEditModal();
    } catch (error) {
        showError(error.message);
    } finally {
        loading_spinner.value = false;
    }
}; 


const closeEditModal = () => {
    agendaDescription.value = '';
    attendee.value = null;
    minuteDescription.value = '';
    selectedAgenda.value = null;
    selectedMinute.value = null;
    selectedAttendee.value = null;
};
 
 const openAttendeeModal = (row) => {
    attendee_status.value = row;
    selectedAttendee.value = row;
};

const openAgendaModal = (row) => {
    agenda_status.value = row;
    selectedAgenda.value = row;
};

const openMinutesModal = (row) => {
    minutes_status.value = row;
    selectedMinute.value = row;
};

const OpenEditMeeting = (row) => {
    name.value = row.name,
    date.value = row.date,
    organizer.value = row.organizer,
    meeting_time.value = row.meeting_time,
    duration.value = row.duration,
    location.value = row.location
}


const updating = ref(false);
const updateMeeting = async () => {
    updating.value = true;
    const payload = {
        name: name.value,
        date: date.value,
        organizer_id: organizer.value.id,
        meeting_time: meeting_time.value,
        duration: duration.value,
        location: location.value
    };
    try {
        const response = await axiosInstance.put(`/meetings/${selected_meeting_id.value}`, payload);
        fetchMeetingDetails(selected_meeting_id.value);
        showMessage('Meeting updated successfully!');
        closeEditModal();
        document.getElementById('close_meeting').click();
    } catch (error) {
        showError(error.message);
        console.log(error)
    } finally {
        updating.value = false;
    }
};

const openAttachmentModal = () => {
    selected_meeting_id.value = meeting.id;
};
const handleFileUpload = (e) => {
  attachment.value = e.target.files[0];
};
const attachment = ref('')
const uploadingAttachment = ref(false);
const submitAttendanceAttachment = async () => {
    uploadingAttachment.value = true;

    const formData = new FormData();
    formData.append('attendance_attachment', attachment.value);

    try {
        const response = await axiosInstance.post(
            `/meetings/${selected_meeting_id.value}/attachment`,
            formData,
            { headers: { 'Content-Type': 'multipart/form-data' } }
        );

        fetchMeetingDetails(selected_meeting_id.value);
        showMessage('Attendance attached successfully!');
        closeAttachmentModal();
        document.getElementById('close_attachment').click();
    } catch (error) {
        showError(error.message);
        console.log(error);
    } finally {
        uploadingAttachment.value = false;
    }
};

