// Update the viewAttachment function
const viewAttachment = (attachmentPath) => {
    currentAttachmentPath.value = attachmentPath;
    const baseUrl = process.env.VUE_APP_API_BASE_URL || window.location.origin;
    const baseDomain = baseUrl.replace('/api/v1/', '').replace('/api/v1', '');
    const cleanPath = attachmentPath.replace(/^[\/\\]+/, '');
    const cleanBaseDomain = baseDomain.replace(/\/$/, '');
    attachmentUrl.value = `${cleanBaseDomain}/${cleanPath}`;
};

const closeViewAttachmentModal = () => {
    currentAttachmentPath.value = '';
    attachmentUrl.value = '';
    document.getElementById('close_view_attachment').click();  
};

